import {request} from "../axios/request";

const baseUrl = '/api/admin'

const EssaysApi = {}

EssaysApi.All = () => {
  return request({
    url: `${baseUrl}/allEssays`,
    method: 'get'
  })
}

EssaysApi.addEssay = content => {
  return request({
    url: `${baseUrl}/SaveEssay`,
    method: 'post',
    params: {
      content
    }
  })
}

EssaysApi.update = essay => {
  return request({
    url: `${baseUrl}/updateEssay`,
    method: 'put',
    data: essay
  })
}

EssaysApi.delete = id => {
  return request({
    url: `${baseUrl}/deleteEssay`,
    method: 'delete',
    params: {
      id
    }
  })
}

EssaysApi.query = params => {
  return request({
    url: `${baseUrl}/queryEssays`,
    method: 'get',
    params: params
  })
}

export default EssaysApi